* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
  /* background-color:lightblue; */
  /* opacity: 10; */
  /* background-image: url(./image/IMG_20200827_174724.jpg); */
  background-attachment: fixed;
  /* opacity: 0.9; */
}

.heading-style {
  text-align: center;
  font-size: x-large;
  color: #db3c3c;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  font-size: 1.5rem;
  margin-top: 50px;

}

.Display_card {
  /* display: inline-flex; */
}

h6 {
  color: rgb(234, 78, 30);
  font-size: larger;
}

.cards {
  display: inline-block;

}

.card {
  border-radius: 25px;
  position: relative;
  /* padding: 25px 15px; */
  background-color: rgb(92, 88, 88);
  margin: 100px;
  /* height: 200px; */
  box-shadow: 15px 15px 15px 15px #696767;
  text-align: left;
}

.card__img {
  display: flex;
  height: 300px;
  width: 350px;
  flex-wrap: nowrap;
  background-color: chocolate;
  /* opacity: 0.8; */
}

img:hover{
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}

.card__info {
  background-color: aliceblue;
  margin: -2px;
  padding: 15px 0px 5px 0px;
  /* text-align: center; */
}

.card__category {
  background-color: aliceblue;
  text-align: center;
  color: silver;
  font-size: larger;
  font-weight: 300;
  padding-left: 10px;
}

.card__title {
  background-color: aliceblue;
  /* padding:0px 0px 0px 12px; */
  /* text-align: center; */
  padding-left: 10px;
  flex: 1;
}

@media screen and (max-width: 800px) {
  .column {
    flex: 50%;
    max-width: 50%;
  }
}

button {
  margin-left: 90px;
  background-color: rgb(168, 157, 157);
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

button:hover {
  opacity: .6;
  background-color: rgb(234, 67, 67);
}



























